import React, { Component, Fragment } from "react";
import { connect } from "react-redux";

import lazyLoadImages from "../../utilities/imagesLazyLoad";
import * as actionCreators from "../../store/actions/index";
import Button from "../../components/UI/Button/Button";
import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/Spinner";
import Input from "../../components/UI/Input/Input";
import "./Idioms.css";

class Idioms extends Component {
  state = {
    form: [
      {
        elementType: "input",
        elementConfig: {
          type: "radio",
          name: "idiom",
          checked: false,
          placeholder: "Blue",
          id: "Blue"
        },
        value: "",
        touched: false
      },
      {
        elementType: "input",
        elementConfig: {
          type: "radio",
          name: "idiom",
          checked: false,
          placeholder: "Green",
          id: "Green"
        },
        value: "",
        touched: false
      },
      {
        elementType: "input",
        elementConfig: {
          type: "radio",
          name: "idiom",
          checked: false,
          placeholder: "Red",
          id: "Red"
        },
        value: "",
        touched: false
      },
      {
        elementType: "input",
        elementConfig: {
          type: "radio",
          name: "idiom",
          checked: false,
          placeholder: "Black",
          id: "Black"
        },
        value: "",
        touched: false
      }
    ],
    currentWord: 0,
    correctAnswer: "",
    correctWords: 0,
    url: `https://spreadsheets.google.com/feeds/list/12iezPG7J07988QvhFXreRS2UNqjXa30B0l_rS8XJtSU/${8}/public/values?alt=json`
  };

  componentDidMount() {
    this.props.fetchData(
    this.state.url
    );
  }

  componentDidUpdate(prevProps, prevState) {
    
  }

  shouldComponentUpdate(nextProps, nextState) {
    return !this.state.currentWord ||
      (this.props.data && this.props.data[nextState.currentWord])
      ? true
      : false;
  }

  onChangeHandler = e => {
    e.preventDefault();
    let label = e.target.previousElementSibling;
    if (
      this.props.data[this.state.currentWord] &&
      e.target.placeholder.toUpperCase() ===
        this.props.data[this.state.currentWord].opposite.toUpperCase()
    ) {
      label.classList.add("correct");
      this.setState(prevState => {
        return {
          correctAnswer: true,
          correctWords: prevState.correctWords + 1
        };
      });
    } else {
      label.classList.add("wrong");
      this.setState(prevState => {
        return {
          correctAnswer: true
        };
      });
    }
    this.timer = setTimeout(() => {
      label.classList.remove("correct", "wrong");
      this.setState(prevState => ({
        correctAnswer: false,
        currentWord: prevState.currentWord + 1
      }));
    }, 3500);
  };

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  reloadTask = () => {
    this.setState(() => ({ currentWord: 0, correctWords: 0 }));
  };

  render() {
    let data = null;
    if (this.props.loading) {
      data = <Spinner />;
    } else if (this.props.error) {
      data = <div>Oops, something went wrong...</div>;
    } else if (this.props.data) {
      data = this.props.data[this.state.currentWord].word;
      if (this.state.correctAnswer) {
        data = (
          <Fragment>
            <span>{this.props.data[this.state.currentWord].fullsentence}</span>
            <br/>
            <span className='explanation'>{this.props.data[this.state.currentWord].explanation}</span>
          </Fragment>
        );
      }
    }
    return (
      <div className="wrapper">
        <div className="scores">
          <p>You've scored:</p>
          <p>
            <span>{this.state.correctWords}</span> out of
            <span> {this.props.data ? this.props.data.length : ""}</span>
            <br />
            questions
          </p>
        </div>
        <div className="playground">
          {/* <form action="#"> */}
          <div className="question">{data}</div>
          <div>
            {this.state.form.map(elem => (
              <Input
                change={this.onChangeHandler}
                data={elem.elementConfig}
                key={elem.elementConfig.placeholder}
                label={elem.elementConfig.placeholder}
              />
            ))}
              <Button classes="Success" click={this.reloadTask}>Wanna try again?</Button>
            <progress
              max={this.props.data ? this.props.data.length : "1"}
              value={this.state.currentWord + 1}
              step="1"
            />
            </div>
          {/* </form> */}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.wordsConstructor.fetchedData,
    loading: state.wordsConstructor.loading,
    error: state.wordsConstructor.error,
    url: state.wordsConstructor.url
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchData: url => dispatch(actionCreators.asyncFetchStart(url)),
    clearData: () => dispatch(actionCreators.clearData)
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Idioms);



